<template lang="html">
  <div class="editor">
    <div ref="toolbar" class="toolbar">
    </div>
    <div ref="editor" class="text">
    </div>
  </div>
</template>

<script>
import emojiJson from "../../static/data/emoji.json";
import emojiJson2 from "../../static/data/emoji2.json";
import E from "wangeditor";
export default {
  name: "editoritem",
  data() {
    return {
      // uploadPath,
      editor: null,
      info_: null,
      emojiList: emojiJson,
      emojiList2: emojiJson2
    };
  },
  model: {
    prop: "value",
    event: "change"
  },
  props: {
    value: {
      type: String,
      default: ""
    },
    isClear: {
      type: Boolean,
      default: true
    }
  },
  watch: {
    isClear(val) {
      // 
      if (val) {
        this.editor.txt.clear();
        this.info_ = null;
      }
    },
    value: function(value) {
      if (value !== this.editor.txt.html()) {
        this.editor.txt.html(this.value);
      }
    }
    //valuevalue
  },
  mounted() {
    this.seteditor();
    this.editor.txt.html(this.value);
  },
  methods: {
    getContent() {
      return this.editor.txt.html();
    },
    seteditor() {
      // http://192.168.2.125:8080/admin/storage/create
      this.editor = new E(this.$refs.toolbar, this.$refs.editor);
      // this.editor.customConfig.uploadImgShowBase64 = false; // base 64 
      // this.editor.customConfig.uploadImgServer =
      //   "http://otp.cdinfotech.top/file/upload_images"; // 
      // this.editor.customConfig.uploadImgHeaders = {}; //  header
      // this.editor.customConfig.uploadFileName = "file"; // 
      // this.editor.customConfig.uploadImgMaxSize = 2 * 1024 * 1024; //  2M
      // this.editor.customConfig.uploadImgMaxLength = 6; //  3 
      // this.editor.customConfig.uploadImgTimeout = 3 * 60 * 1000; // 

      // 
      this.editor.customConfig.menus = [
        // "head", // 
        // "bold", // 
        // "fontSize", // 
        // "fontName", // 
        // "italic", // 
        // "underline", // 
        // "strikeThrough", // 
        // "foreColor", // 
        // "backColor", // 
        "link", // 
        // "list", // 
        // "justify", // 
        // "quote", // 
        "emoticon", // 
        // "image", // 
        // "table", // 
        // "video", // 
        // "code", // 
        "undo" // 
        // "redo" // 
        // "fullscreen" // 
      ];

      // this.editor.customConfig.uploadImgHooks = {
      //   fail: (xhr, editor, result) => {
      //     // 
      //   },
      //   success: (xhr, editor, result) => {
      //     // 
      //   },
      //   timeout: (xhr, editor) => {
      //     // 
      //   },
      //   error: (xhr, editor) => {
      //     // 
      //   },
      //   customInsert: (insertImg, result, editor) => {
      //     // 
      //     //resultdata[{url:""},...]
      //     // console.log(result.data[0].url)
      //     //insertImg()
      //     //
      //     // for (let i = 0; i < 1; i++) {
      //     // console.log(result)
      //     let url = "http://otp.cdinfotech.top" + result.url;
      //     insertImg(url);
      //     // }
      //   }
      // };
      this.editor.customConfig.emotions = [
        {
          title: "QQ",
          type: "image",
          content: this.emojiList
        },
        {
          title: "",
          type: "image",
          content: this.emojiList2
        }
      ];
      this.editor.customConfig.onchange = html => {
        this.info_ = html; // 
        this.$emit("change", this.info_); // 
      };
      // 
      this.editor.create();
    }
  }
};
</script>

<style>
.editor {
  width: 100%;
  margin: 0 auto;
  position: relative;
  z-index: 0;
}
.toolbar {
  border: 1px solid #ccc;
}
.text {
  border: 1px solid #ccc;
  min-height: 180px;
}
.w-e-text {
  height: 180px;
  min-height: 180px !important;
  max-height: 180px;
  overflow-y: auto;
  overflow-y: hidden;
}
.text {
  border-radius: 15px;
}
.toolbar {
  width: 150px;
  border-radius: 15px;
}
</style>


