import axios from 'axios'
import { throwErr } from './utils' // utils http
import { Message, MessageBox } from 'element-ui'
// 
axios.interceptors.request.use(config => {
  config.timeout = 10 * 1000 // 
  config.headers = {
    'Content-Type': 'application/json; charset=utf-8'
  };
  return config
}, error => {
  return Promise.reject(error)
})
// 
axios.interceptors.response.use(
  response => {
    if (response.data.code === 0) {   // code0
      return response.data // Promise.resolve 
    } else {
      return response.data// Promise.reject()
    }
  },
  error => {
    if (error && error.response) {
      let res = {}
      res.code = error.response.status
      res.msg = throwErr(error.response.status, error.response) // throwErr http utils      
      Message({
        type: 'error',
        duration: 5 * 1000,
        message: "Bug,!"
      })
      return Promise.reject(res)
    }
    return Promise.reject(error)
  }
)
export default function request(method, url, data) {  //  request API   
  let BaseUrl = "https://www.zswblog.xyz" + url;// 
  method = method.toLocaleLowerCase()   // RESTful API  post get delete
  if (method === 'post') {
    return axios.post(BaseUrl, data)    // axiospost json
  } else if (method === 'get') {
    return axios.get(BaseUrl, {
      params: data
    })
  } else if (method === 'delete') {
    return axios.delete(BaseUrl, {
      params: data
    })
  }
}


